/**
 * loginController as loginCtrl
 * ログイン
 */

(function() {
    'use strict';

    angular
        .module('learnApp')
        .controller('loginController', loginController);

    loginController.$inject = ['$scope','ApiService'];
    function loginController($scope,ApiService) {
        var loginCtrl = this;

        loginCtrl.method = {
            clickSubmit: clickSubmit //ログインボタンクリック
        }

        loginCtrl.value = {
            flag: {
                error: false //ログインエラーフラグ
            },
            user: {}
        }

        require('./../../css/sign.css');
        init();

        /**
         * 初期化メソッド
         */
        function init(){
            $scope.indexCtrl.method.clickNav(1);
        }

        /**
         * ログインボタンクリックメソッド
         */
        function clickSubmit(){
            //ログイン情報のポスト
             ApiService.login(loginCtrl.value.user).success(
                function(data) {
                    console.log(data)
                    //ログイン成功
                    if (data.status == 0) {
                        $scope.indexCtrl.value.user = data.data;
                        document.cookie = 'userId=' + data.data.userId;
                        $scope.indexCtrl.value.userId = data.data.userId;
                        window.location.href = './#/myPage/';
                    } else {
                        loginCtrl.value.flag.error = true;
                    }
                },
                function (data) {
                    loginCtrl.value.flag.error = true;
                }
            )
        }
    }
}());



